@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
 
:root{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  background-color: #FCF8E8;
}



.head{
  background-color: #146356;
  color: #FCF8E8;

}

.head > h1 {
  padding-top: 0;
  margin-top: 0;
  padding: .5rem 1rem;
  font-variant: small-caps;
  font-weight: 700;

}



.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: white;
  outline: .01rem solid #146356;
  outline-offset: -.01rem;
  color: #146356;
  vertical-align:middle;
}

.search-bar:focus {
  outline: .15rem solid #146356;
  outline-offset: -.15rem;
}


.search-button{
  background: #146356;
  border: none;
  padding: 10px 20px;
  color: white;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}