

.recipe{
    border-radius: .5rem;
    box-shadow: 0px 5px 20px rgba(71, 71, 71,.05);
    margin: 20px;
    width: 28rem;
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
    background: white;
    word-wrap: break-word;
    align-items: center;
    min-width: 30%;
}

.recipe_desc {
    width: 95%;
    background-color: #FDFAF6; 
    margin: .8rem;
    padding: .5rem 0;
    border-radius: .3rem;
    border: 1px solid rgb(244, 244, 244);
}

.rec_header {
    width: 100%;
    padding: 0;
    margin: 0;
}

.rec_header h2{
    font-size: 1.2rem;
    background-color: white;
    margin-top: 0;
    margin-bottom: 0;
    padding: .4rem .6rem;
    color: #343A40;
    margin: 0 .5rem;
    border-radius: .2rem;
    font-weight: 500;
    border: 1px solid rgb(232, 232, 232);
    
}

hr {
    color: #146356;
    /* padding: 1px; */
    /* margin: 1px; */
}
.steps_container {
    width: 100%;
    padding-right: 1rem;
    display: block;
    font-size: .9rem;
    font-family: 'Lato', sans-serif;
}

.steps_cnt {
    color: rgb(97, 97, 97);
    font-weight: 600;
    /* background-color: #A3DA8D; */
}
.steps_container ol {
    
}


.steps {
    color: rgb(97, 97, 97);
    font-weight: 400;
    padding: 0 0;
    margin: .3rem 0;
    margin-left: .5rem;
    line-height:2ch;
    /* background-color: #A3DA8D; */
}


.img_container {
    width: 95%;
    /* padding: 2rem; */
    display: flex;
    flex-wrap: wrap;
    /* align-items: center; */
    justify-content: center;
    padding:  1rem 0;
    /* padding-left: ; */
}

.image{
    /* border-radius: 50%; */
    border-radius: .5rem;
    width: 200px;
    height: 200px;
    margin: .1rem;
    margin-right: .5rem;
    /* box-shadow: 0px 5px 20px rgb(28, 50, 75); */
    background-position: cover;
}

.recipe_info {
    font-size: .9rem;
    /* min-width:20rem ; */

    max-width: 13rem;
}

@media only screen and (max-width: 960px) {
    
    .recipe_info {
        min-width:100% ;
    }

    .image{

        width: 100%;
        height: 200px;
        /* margin: .1rem;
        margin-right: .5rem; */
        /* box-shadow: 0px 5px 20px rgb(28, 50, 75); */
        background-size: cover;
    }
}

.recipe_info th {
    /* padding-left: 1rem; */
    color: grey ;
    text-align: start;
    font-size: .8rem;
    font-weight: 400;
}

.recipe_info td {
    font-size: .8rem;
    color: rgb(51, 51, 51) ;
}

.recipe_info_title {
    /* text-align: center; */
    background-color: #F3C892;
    color: #FCF8E8;
    padding: .2rem .5rem;
    margin: .5rem 0rem;
    border-radius: .2rem;
}
